
*{margin:0px;padding:0;-webkit-box-sizing:border-box;box-sizing:border-box
}
body{width:100vw;height:100vh;background:radial-gradient(circle, #070011, #250060)
}
body>.main{width:100%;max-width:940px;display:-webkit-box;display:-ms-flexbox;display:flex;padding:40px calc((100% - 940px) / 2)
}
body>.main>.left{width:300px
}
body>.main>.left .title{width:300px;height:111px;font-size:48px;font-weight:bold;color:#ff0093
}
body>.main>.left .current{width:148px;height:27px;margin-top:24px;background-color:#8000ff;color:#250060;font-size:16px;line-height:27px;text-align:center
}
body>.main>.left .noob{width:220px;margin-top:8px;font-size:36px;color:#fff;font-weight:bold
}
body>.main>.left .noob .top{width:215px;border-bottom:4px solid #8000ff
}
body>.main>.left .noob .bottom{width:100px;border-bottom:4px solid #8000ff
}
body>.main>.left .img{margin-top:20px;width:285px;height:210px;margin-left:15px
}
body>.main>.left .img img{width:100%;height:100%
}
body>.main>.left .note{width:300px;height:182px;margin-top:12px;font-size:16px;line-height:16px;border:1px solid #ff0093
}
body>.main>.left .note .text{width:248px;height:91px;margin-top:61.77px;margin-left:24px
}
body>.main>.left .note .text>.white{color:white
}
body>.main>.left .note .text>.red{color:#ff0093
}
body>.main>.right{width:calc(940px - 300px);margin-left:20px;display:-webkit-box;display:-ms-flexbox;display:flex
}
body>.main>.right>.center{width:315px;height:100vh
}
body>.main>.right>.center .basics{display:-webkit-box;display:-ms-flexbox;display:flex;margin-bottom:24px;position:relative
}
body>.main>.right>.center .basics>.basics-left{width:100px;margin-right:12px
}
body>.main>.right>.center .basics>.basics-left:after{content:'';position:absolute;bottom:-23px;left:16%;-webkit-transform:translateX(-50%);transform:translateX(-50%);width:0;height:0;border-style:solid;border-width:20px 35px 0 35px;border-color:#35006a transparent transparent transparent
}
body>.main>.right>.center .basics>.basics-left:before{content:'';position:absolute;display:block;width:48px;height:calc(100% - 20px - 20px);top:45px;left:16%;-webkit-transform:translateX(-50%);transform:translateX(-50%);background-color:#35006A
}
body>.main>.right>.center .basics>.basics-left img{position:relative;width:100%;height:100px;z-index:5
}
body>.main>.right>.center .basics>.basics-right{width:204px;display:inline-block
}
body>.main>.right>.center .basics>.basics-right>.detail{width:60px;height:100px;display:inline-block;margin-right:4px
}
body>.main>.right>.center .basics>.basics-right>.detail>.top{width:60px;height:60px;background-color:#250060
}
body>.main>.right>.center .basics>.basics-right>.detail>.top.active{border:2px solid #ff0093;background-color:#6900D1
}
body>.main>.right>.center .basics>.basics-right>.detail>.top>img{width:31.67px;height:33.33px;margin-top:12px;margin-left:13px;color:white
}
body>.main>.right>.center .basics>.basics-right>.detail>.bottom>.detail{width:60px;height:15px;display:-webkit-box;display:-ms-flexbox;display:flex;text-align:center;margin-top:5px
}
body>.main>.right>.center .basics>.basics-right>.detail>.bottom>.detail>.number{width:21px;height:16px;font-size:14px;color:white
}
body>.main>.right>.center .basics>.basics-right>.detail>.bottom>.detail>img{margin-left:12.41px;margin-right:3.41px;width:13.18px;height:13.33px;line-height:13.17px
}
body>.main>.right>.center .basics>.basics-right>.complete .top{position:relative
}
body>.main>.right>.center .basics>.basics-right>.complete .top:before{content:'';position:absolute;right:0;top:0;display:block;width:0;height:0;border-style:solid;border-width:0 15px 15px 0;border-color:transparent #8000FF transparent transparent
}
body>.main>.right>.center .basics>.basics-right>.canClick{cursor:pointer
}
body>.main>.right>.right{width:300px;height:700px;border:2px solid #ff0093
}
body>.main>.right>.right>.top{width:296px;height:306px;background:#8000ff
}
body>.main>.right>.right>.top>.img{width:160px;height:160px
}
body>.main>.right>.right>.top>.img img{width:100%;height:100%;margin-top:24px;margin-left:68px
}
body>.main>.right>.right>.top>.text{line-height:36px;color:white;font-size:34px;line-height:36px;margin-top:28px;width:100%;text-align:center;display:grid
}
body>.main>.right>.right>.bottom{width:100%;height:calc(700px - 310px);background:#6900de
}
body>.main>.right>.right>.bottom>.main{color:white;display:inline-block;text-align:center;width:196px;margin-left:calc((296px - 196px) / 2)
}
body>.main>.right>.right>.bottom>.main .recomended,body>.main>.right>.right>.bottom>.main .optional{width:100%;margin-top:27px
}
body>.main>.right>.right>.bottom>.main .recomended>.img,body>.main>.right>.right>.bottom>.main .optional>.img{width:32.95px;height:33.33px;display:block;margin:auto
}
body>.main>.right>.right>.bottom>.main .recomended>.img img,body>.main>.right>.right>.bottom>.main .optional>.img img{width:100%;height:100%
}
body>.main>.right>.right>.bottom>.main .recomended>.title,body>.main>.right>.right>.bottom>.main .optional>.title{width:100%;height:30px;text-align:center
}
body>.main>.right>.right>.bottom>.main .recomended>.title span,body>.main>.right>.right>.bottom>.main .optional>.title span{font-size:16px;line-height:16px;margin-top:5px
}
body>.main>.right>.right>.bottom>.main .recomended ul,body>.main>.right>.right>.bottom>.main .optional ul{list-style:none
}
body>.main>.right>.right>.bottom>.main .recomended ul>li,body>.main>.right>.right>.bottom>.main .optional ul>li{border:2px solid white;width:100%;overflow:hidden;list-style:none;height:35px;text-align:center;font-size:16px;line-height:30px;margin-bottom:5px;cursor:pointer
}
body>.main>.right>.right>.bottom>.main .recomended ul>li:hover,body>.main>.right>.right>.bottom>.main .recomended ul>li.active,body>.main>.right>.right>.bottom>.main .optional ul>li:hover,body>.main>.right>.right>.bottom>.main .optional ul>li.active{border:2px solid #ff0093;background:#ff0093
}
body .done{opacity:0.6
}
body .cannotuse{opacity:0.2
}
